html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  line-height: 26px;
  font-weight: 400;
  font-style: normal;
  &.lock {
    overflow: hidden;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}
.container-sm {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width-sm);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn-outline {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  color: var(--whiteColor);
  padding: 15px 0;
  border: 2px solid var(--accentColor);
  position: relative;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 0%;
    height: 100%;
    background-color: var(--accentColor);
    z-index: -1;
    transition: all 0.3s ease 0s;
  }
  &:hover {
    &:after {
      content: '';
      width: 100%;
    }
  }
  @media (max-width: 992px){
      padding: 10px 0;
  }
  @media (max-width: 480px){
      padding: 8px 0;
      font-size: 14px;
  }
}


.btn {
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: var(--whiteColor);
  padding: 15px 0;
  border: 2px solid var(--blackColor);
  background-color: var(--blackColor);
  transition: all 0.3s ease 0s;
  &:hover {
    background-color: var(--whiteColor);
    border: 2px solid var(--accentColor);
    color: var(--blackColor);
  }
}
